export class HttpClient {
    constructor() {
        this.baseUrl = __PROD__ ? __SERVER_URL__ : "http://localhost:5000";
        this.headers = {
            "Content-Type": "application/json",
        };
    }
    static getInstance() {
        if (!HttpClient.instance) {
            HttpClient.instance = new HttpClient();
        }
        return HttpClient.instance;
    }
    async get(url, params = {}) {
        const processedUrl = this.processUrl(url);
        const search = new URLSearchParams(params);
        const response = await fetch(`${this.baseUrl}/${processedUrl}?${search}`, {
            method: "GET",
            credentials: "include",
        });
        return response.json();
    }
    async post(url, data = {}, params = {}, headers = this.headers) {
        const processedUrl = this.processUrl(url);
        const search = new URLSearchParams(params);
        const response = await fetch(`${this.baseUrl}/${processedUrl}?${search}`, {
            method: "POST",
            headers,
            credentials: "include",
            body: JSON.stringify(data),
        });
        return response.json();
    }
    processUrl(url) {
        return url.startsWith("/") ? url.slice(1) : url;
    }
}
export const api = HttpClient.getInstance();
